.Profile {
  position: relative;
  padding-bottom: 3em;
}

.Profile-logout {
  font-size: 0.75em;
  position: absolute;
  top: 4.5em;
  right: 0;
  color: var(--secondary-color);
  text-decoration: underline;
}

.Profile > h2 {
  text-align: center;
}

.Profile > h2 + .line {
  margin-top: 1.5em;
}

.Profile > .line {
  display: block;
  width: 1.5em;
  margin: 4.5em auto;
}


.Profile-details {
  display: flex;
  flex-wrap: wrap;
}

.Profile-details > * {
  flex: 1 0 100%;
}

.Profile-details > * + * {
  margin-top: 3em;
}

.Profile-details .inline-edit {
  color: var(--secondary-color);
}

.Profile-details .inline-edit > button {
  top: -1.875em;
}

.Profile-details .inline-edit li + li,
.Profile-details .inline-edit + .inline-edit {
  margin-top: 0.375em;
}

#Profile-info-address > .inline-edit > button {
  top: 0;
  height: 1.125em;
  width: 1.125em;
}

#Profile-info-address form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
#Profile-info-address form > div {
  flex: 0 0 45%;
}
#Profile-info-address form > div:first-child, 
#Profile-info-address form > div:last-child {
  flex: 0 0 100%;
}

.Profile-change-password {
  margin-top: 1.5em;
}

.Profile-change-password > button {
  font-size: 0.75em;
  position: static;
  width: auto;
  height: auto;
  color: var(--secondary-color);
  text-decoration: underline;
}


.Profile-orders {
  color: var(--secondary-color);
}

.Profile-orders > h2 {
  color: var(--primary-color);
  text-align: center;
}

.Profile-orders > p {
  margin-top: 1.5em;
  text-align: center;
}

.Profile-orders > p:first-of-type {
  text-align: left;
}


.Profile-orders > button {
  margin-top: 3em;
}

.Profile-orders > button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}


.Profile .Order {
  margin-top: 3em;
}


.Order:not(:last-of-type) {
  padding-bottom: 3em;
  border-bottom: 1px solid var(--secondary-color2);
}

.Order > h4 {
  color: var(--primary-color);
}

.Order > h4 > span {
  margin-left: 0.5em;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  text-decoration: underline;
}

.Order-status {
  font-size: 0.875em;
  display: inline-block;
  padding: 0.1875em 1.875em;
  background-color: var(--brand-color);
  color: #FFF;
}

.Order > .Order-status {
  margin-top: 1.5em;
  margin-left: -1.125em;
}

.Order > p {
  margin-top: 1.5em;
  font-family: 'Lato', sans-serif;
}

.Order > p > span {
  font-size: 1.125em;
  font-weight: 700;
}

.Order-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 2.25em;
}

.Order-actions > a {
  flex: 0 0 45%;
  max-width: 9em;
}

.Order-actions:first-of-type > a {
  text-align: center;
}

.Order-actions:not(:first-of-type) > a {
  font-size: 0.75em;
  text-decoration: underline;
}

.Order-actions:not(:first-of-type) > a:last-child {
  text-align: right;
}

.Profile-orders-details {
  display: flex;
  flex-wrap: wrap;
}

.Profile-orders-details > * {
  flex: 1 0 100%;
}

@media only screen and (min-width: 51em) {
  .Profile-details {
    justify-content: space-around;
  }

  .Profile-details > * {
    flex: 1 0 21em;
    max-width: 40%;
    border: 1px solid var(--secondary-color2);
    padding: 1.875em;
  }

  .Profile-details > * + * {
    margin-top: 0;
  }

  .Profile-orders-details {
    justify-content: space-around;
  }

  .Profile-orders-details > * {
    flex: 1 0 21em;
    max-width: 40%;
  }
}
