@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&family=Playfair+Display:wght@400;700&family=Playfair+Display+SC:wght@700&family=Lato:wght@300&display=swap);
/* 0.1875 0.375 0.75 1.5 */
:root {
  font-family: Roboto, sans-serif;
  line-height: 1.5;
  font-size: calc(1rem + 0.1vw);
}

/* [Tweaks] */
* {
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: inherit;
}

:focus {
  outline: 2px solid;
}

[hidden] {
  display: none !important;
}

[disabled] {
  cursor: not-allowed;
}

[disabled]:hover {
  background-color: inherit;
}

/* reset margin and padding */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li,
div,
figure,
hr,
button,
input,
textarea,
select,
option,
fieldset {
  margin: 0;
  padding: 0;
}

fieldset {
  border: 1px solid currentColor;
}


/* Typhography */
h1, h2, h3, h4, h5, h6 {
  margin-bottom: 0.1875em;
}

p + h2, p + h3, p + h4, p + h5, p + h6 {
  margin-top: 1.5em;
}
 

/* Link */
a {
  cursor: pointer;
  color: currentColor;
}

a.button,
a.button:hover, 
a.button:focus, 
a.button:active {
  text-decoration: none;
}

a:hover,
a:focus,
a:active {
  text-decoration: underline;
}

/* tweak for right outline */
a > img,
a > svg {
  vertical-align: middle;
}

/* Media */
img, video, svg {
  border-style: none;
  max-width: 100%;
  height: auto;
}


/* List */
ol,
ul {
  list-style: none;
}


/* Table */
table {
  border-collapse: collapse;
}

th, td {
  padding: 0.125em 0.25em;
}

th {
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

td {
  text-align: start;
}

tbody > tr:not(:last-of-type) {
  border-bottom: 0.03125em solid;
}

tr > *:not(:last-of-type) {
  border-right: 0.03125em solid;
}

tr:hover > td {
  background-color: #eee;
}


/* Form */
button {
  border: 0;
  background: #eee;
}

textarea {
  vertical-align: top;
}

textarea,
select,
input {
  background: #fff;
  border: 1px solid currentColor;
}

button,
select,
textarea,
input {
  color: #000;
  font: inherit;
}

button,
select {
  cursor: pointer;
}

/* Code */
code {
  font-family: "Source Code Pro", Consolas, monospace;
}

/* - html is responsive by default, but css not */
/* - use minimum classes */
/* - use child/sibling selectors for styling */

/* size */
/* - extrinsic sizing means certain sizing elements */
/* - intrinsic sizing means relative sizing elements */

/* size values */
/* - pt/mm/in/... uses only for print */
/* - 1 em equals 100% of current element's font-size */
/* - 1 rem equals 100% of root element's font-size */
/* - use em for almost everything */
/* - use em for media query */

/* flex */
/* - flex better (more simple) for responsive than grid */
/* - flex: 1 0 200px; max-width: 400px; use this for true responsive layout 
 *   where element size will be from 200px to 400px depends of 
 *   free space in a row */
/* - flex-grow defines whether element grow it size */
/* - flex-shrink defines whether element shrink it size */
/* - flex-basis defines minimum size of element */
/* - flex: 1 0 200px means flex-grow: 1, flex-shrink: 0, flex-basis: 200px */

/* grid */
/* - justify/align-content for grid item */
/* - justify/align-items for grid item content */
/* - for justify-* should be defined width property */
/* - for align-* should be defined height property */

/* hiding */
/* - exclude node from DOM: display: none */
/* - hide node: visibility: hidden */
/* - use visibility for animation */
/* - exclude node from accessibility tree: aria-hidden="true" */

:root {
  --primary-color: #182336;
  --secondary-color: #939393;
  --secondary-color2: #D3D3D3;
  --brand-color: #CDAE62;
  --brand-color-darker: #BA9357;
  --brand-color2: #182C58;
  --accent-color: #E16842;
  font-family: 'Playfair Display', serif;
}

:focus {
  outline-color: #182C58;
  outline-color: var(--brand-color2);
}

html {
  color: #182336;
  color: var(--primary-color);
}

a {
  text-decoration: none;
}


form > div {
  position: relative;
}

form > div + div {
  margin-top: 0.5em;
}

form > div:last-child {
  display: flex;
  margin-top: 3em;
  justify-content: space-between;
}

form > div:last-child > button {
  flex: 0 0 45%;
  max-width: 9em;
}


form > div > input {
  width: 100%;
  padding: 0.5em 1em;
  border: 1px solid #D3D3D3;
  border: 1px solid var(--secondary-color2);
  color: #182336;
  color: var(--primary-color);
  background: transparent;
  font-family: 'Lato', sans-serif;
}

form > div > input::-webkit-input-placeholder {
  font-family: 'Lato', sans-serif;
  color: #939393;
  color: var(--secondary-color);
}

form > div > input:-ms-input-placeholder {
  font-family: 'Lato', sans-serif;
  color: #939393;
  color: var(--secondary-color);
}

form > div > input::-ms-input-placeholder {
  font-family: 'Lato', sans-serif;
  color: #939393;
  color: var(--secondary-color);
}

form > div > input::placeholder {
  font-family: 'Lato', sans-serif;
  color: #939393;
  color: var(--secondary-color);
}

form > div > label {
  font-size: 0.875em;
  font-family: 'Lato', sans-serif;
  position: absolute;
  top: 0.75em;
  color: #939393;
  color: var(--secondary-color);
  text-transform: capitalize;
}

form > div > input,
form > div > textarea, 
form > div > select {
  background: transparent;
}

form > div > label + input {
  margin-top: 1.875em;
}


.main-title {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
}

.secondary-title {
  font-size: 1.5em;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  color: #CDAE62;
  color: var(--brand-color);
}

.secondary-title > span:not(.line) {
  flex: 0 0 6.75em;
  text-align: center;
}

.secondary-title > .line {
  display: block;
  flex: 0 0 1.5em;
  margin-top: auto;
  margin-bottom: 0.625em;
}

.accent-btn,
.primary-btn {
  display: inline-block;
  text-decoration: none;
  padding: 1em 1.125em;
}

.accent-btn:focus,
.primary-btn:focus {
  text-decoration: none;
}

.accent-btn {
  background-color: #E16842;
  background-color: var(--accent-color);
  border: 1px solid #E16842;
  border: 1px solid var(--accent-color);
  color: #E5E5E5;
}

.primary-btn {
  background-color: transparent;
  color: #939393;
  color: var(--secondary-color);
  border: 1px solid currentColor;
}

.line {
  display: inline-block;
  width: 100%;
  border-top: 1px solid currentColor;
  vertical-align: middle;
}

.stepper {
  display: flex;
  flex-wrap: nowrap;
}

.stepper > button {
  flex: 0 0 1.75em;
  background: transparent;
  color: var(--secondary-color);
}

.stepper > input {
  flex: 0 0 1.75em;
  max-width: 1.75em;
  text-align: center;
  color: var(--secondary-color);
  border: 1px solid var(--secondary-color2);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}


.PageTitle {
  font-size: 0.75em;
  font-family: 'Lato', sans-serif;
  display: inline-block;
  padding-top: 4.5em;
  padding-bottom: 4.5em;
  text-transform: uppercase;
  color: var(--secondary-color);
}

.inline-edit {
  position: relative;
}

.inline-edit > button {
  position: absolute;
  right: 0;
  background: transparent;
  width: 1.5em;
  height: 1.5em;
}

.inline-edit > button > svg {
  height: 100%;
  fill: var(--secondary-color);
}

.Menu {
  width: 2.25em;
  height: 2.25em;
  margin-left: -0.375em;
  background: transparent;
  z-index: 10;
}

.Menu > span {
  position: relative;
  display: block;
  width: 1.25em;
  height: 2px;
  margin: auto;
  background-color: var(--brand-color);
}

.Menu > span::after,
.Menu > span::before {
  content: "";
  position: absolute;
  left: 0;
  height: 2px;
  background-color: var(--brand-color);
}
  
.Menu > span::after {
  top: 0.375em;
  width: 1.0625em;
}

.Menu > span::before {
  top: -0.375em;
  width: 1.5em;
}

.Menu > .close-btn {
  transform: rotate(45deg);
  width: 1.5em;
}

.Menu > .close-btn::after {
  top: 0;
  width: 1.5em;
  transform: rotate(-90deg);
}

.Menu > .close-btn::before {
  width: 0;
}


.Menu + div {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 9;
  overflow-y: auto;
  background: #111E3A;
  padding-bottom: 3em;
}

.Menu + div > svg {
  display: block;
  width: 9em;
  height: 4.5em;
  margin-left: auto;
  margin-right: auto;
  fill: var(--brand-color);
}

.Menu + div > nav {
  font-size: 1.5em;
  line-height: 2.25;
  margin-top: 3em;
  margin-left: 2.25em;
  color: var(--secondary-color);
}

.Menu + div > form {
  font-size: 1.5em;
  margin-top: 3em;
  margin-left: 2.25em;
}

.Menu + div > form select {
  padding: 0.1875em 0.375em;
  color: var(--secondary-color);
}

.Menu-nested {
  background: transparent;
  color: currentColor;
}

.Menu-nested > span {
  font-size: 1.25;
  display: inline-block;
  margin-left: 0.375em;
}

.Menu-nested + ul {
  font-size: 1em;
  padding-left: 2.25em;
}

.Layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.Header {
  position: fixed;
  left: 0;
  right: 0;
  display: flex;
  height: 4.5em;
  padding-left: 1em;
  padding-right: 1em;
  align-items: center;
  justify-content: space-between;
  background-color: #111E3A;
  color: var(--brand-color);
  z-index: 10;
}

.Header > a {
  width: 6em;
  justify-self: center;
}

.Header > a > svg {
  fill: var(--brand-color);
  height: 1.5em;
}

.Header > div:last-child {
  text-align: right;
}

.Header > div:last-child > a + a {
  margin-left: 0.5em;
}

.Header > div:last-child > a > svg {
  height: 1.25em;
  width: 1.25em;
  fill: var(--brand-color);
}

.Body {
  margin-top: 4.5em;  
}


.Subscription {
  display: grid;
  grid-row-gap: 3em;
  padding: 4.5em 1em;
  background-color: var(--brand-color);
}

.Subscription > .main-title {
  grid-template-columns: 5fr 1fr;
  grid-column-gap: 1.5em;
}

.Subscription > .main-title > .line {
  margin-top: auto;
  margin-bottom: 1.5em;
}

.Subscription > form > input {
  width: 70%;
  padding: 0.75em 1em;
  background-color: transparent;
  border-color: #3C3A35;
}

.Subscription > form > input::-webkit-input-placeholder {
  color: #3C3A35;
}

.Subscription > form > input:-ms-input-placeholder {
  color: #3C3A35;
}

.Subscription > form > input::-ms-input-placeholder {
  color: #3C3A35;
}

.Subscription > form > input::placeholder {
  color: #3C3A35;
}

.Subscription > form > button {
  width: 30%;
  padding: 0.75em 1em;
  background-color: transparent;
  border: 1px solid currentColor;
  color: #3C3A35;
}

.Subscription > form > input[type="checkbox"] {
  visibility: hidden;
}

.Subscription > form > input[type="checkbox"] + label {
  display: inline-block;
  position: relative;
  z-index: 0;
  width: 100%;
  text-align: center;
  margin-top: 1.5em;
  margin-left: 0.25em;
  padding-left: 1.5em;
}

.Subscription > form > input[type="checkbox"] + label:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 1.5em;
  height: 1.5em;
  border: 1px solid currentColor;
}

.Subscription > form > label > a {
  text-decoration: underline;
}

.Footer {
  margin-top: auto;
  padding: 3em 1em;
  background-color: #1E222C;
  color: var(--secondary-color);
  text-align: center;
}

.Footer-logo {
