}

@media only screen and (min-width: 45em) {
  .Splash {
    font-size: 1.125em;
  }
    
  .Splash > h1 {
    max-width: 75%;
    margin-left: 25%;
    text-align: left;
    justify-content: flex-start;
  }

  .Splash > h1 > small {
